import {Component, OnInit} from '@angular/core';
import {Storage} from "@ionic/storage";
import {ApiService} from "../../services/api.service";
import {AuthService} from "../../services/auth.service";
import {LoaderService} from "../../services/loader.service";
import {Router} from "@angular/router";
import {Location} from '@angular/common';
import {ToastService} from "../../services/toast.service";
import * as moment from 'moment';
import {Platform} from "@ionic/angular";
import {Device} from "@ionic-native/device/ngx";
import {AnalyticsService} from "../../services/analytics.service";
import {TranslateService} from "@ngx-translate/core";
import {EventProviderService} from "../../services/event-provider.service";

@Component({
    selector: 'app-my-volunteering',
    templateUrl: './my-volunteering.page.html',
    styleUrls: ['./my-volunteering.page.scss'],
})
export class MyVolunteeringPage implements OnInit {

    loginData: any;
    volunteeringdata: boolean = false;
    data: any;
    start_time_analytics: any;
    page_name: any = "My Volunteering";
    custom_name: any = "My Volunteering";
    user: any;
    theme: any;
    alertMessage: any;

    constructor(
        private apiService: ApiService,
        private storage: Storage,
        private loader: LoaderService,
        private Auth: AuthService,
        private router: Router,
        private location: Location,
        private toast: ToastService,
        private analytics: AnalyticsService,
        private platform: Platform,
        private device: Device,
        private translate: TranslateService,
        private event_provider: EventProviderService,
    ) {
        // get translated alert message
        let interval = setInterval(() => {
            this.translate.get('ALERTS').subscribe( (res: any) => {
                this.alertMessage = res;
                clearInterval(interval);
            });
        }, 2000)
    }

    ngOnInit() {
        // getting user data
        this.getLoginData();

        // getting theme from local storage
        this.storage.get('theme').then(async res => {
            this.theme = res;
        });
    }

    async getLoginData() {
        this.loginData = await this.Auth.getUser();
        if (this.loginData) {
            // getting Volunteering data
            this.getGroupData();
        }
    }

    // getting Volunteering data
    getGroupData() {

        this.loader.presentLoading().then(() => {
            const body = {
                auth: 'Bearer ' + this.loginData.auth.access_token,
                id: this.loginData.user.id
            };
            // fetching Volunteering data
            this.apiService.getVolunteering(JSON.stringify(body)).subscribe(async (res: any) => {
                if(res.volunteering) {
                    if (res.volunteering[0].length === 0) {
                        this.volunteeringdata = true;
                    } else {

                        this.volunteeringdata = false;
                        this.data = res.volunteering[0];
                        let test;
                        let today;
                        let dd;
                        let mm;

                        const monthNames = ["Jan", "Feb", "Mar", "Apr", "May", "June", "July", "Aug", "Sep", "Oct", "Nov", "Dec"];

                        // formatting date
                        let date = [];
                        for (let x = 0; x < this.data.length; x++) {
                            if (this.data[x].date) {
                                test = this.data[x].date;
                                today = new Date(test);
                                dd = today.getDate();
                                mm = monthNames[today.getMonth()];
                                date.push({dd, mm});
                                this.data[x].start_date = date[x];
                            } else if (this.data[x].date_of_service) {
                                test = this.data[x].date_of_service;
                                this.data[x].service_date = test;
                                today = new Date(test);
                                dd = today.getDate();
                                mm = monthNames[today.getMonth()];
                                date.push({dd, mm});
                                this.data[x].date_of_service = date[x];
                            }
                        }
                    }
                }
                this.loader.stopLoading();
            }, (err: any) => {
                let message = err.message;

                this.toast.presentToastWithOptions((this.alertMessage ? this.alertMessage.error_occured_try_again : 'Error Occured, Try Again'), (this.alertMessage ? this.alertMessage.WarningMessage : 'Warning Message'), 'warning');
                this.loader.stopLoading();
            });
        });
    }

    // navigating to detail page
    openDetail(i) {
        let record_id = null;
        let id = null;

        if (this.data[i].record_id){
            record_id = this.data[i].record_id;
        }
        else if (this.data[i].id){
            id = this.data[i].id;
        }

        this.router.navigate(['my-volunteering-detail', {
            record_id: record_id,
            id: id
        }]);
    }

    // navigating to back page
    gotobackPage(){
        this.location.back();
    }

    setAnalytics() {
        let dev_id = '';
        let device = 'web';
        // preparing device data
        if(this.platform.is('android') || this.platform.is('iphone')) {
            dev_id = this.device.uuid;
            if (dev_id){
                device =  this.platform.platforms()[0];
            }
        }

        // preparing duration data
        let end_time_analytics = moment();
        var duration:any = moment.duration(end_time_analytics.diff(this.start_time_analytics)).asSeconds();

        // preparing user data
        let userId = this.loginData ? this.loginData.user.id : 'guest';

        // preparing data for analytics
        const body = JSON.stringify({
            church_id: this.theme.ch_id,
            latitude: this.theme.latitude ? this.theme.latitude : "",
            longitude: this.theme.longitude ? this.theme.longitude : "",
            timespent: duration,
            module: this.page_name,
            custom_name: this.custom_name,
            ip_address: "",
            device: device,
            device_id: dev_id,
            client_type: userId,
            event_type: ""
        });
        // calling function from service to update Analytics Data
        this.analytics.setAnalytics(body).subscribe((res: any) => {

        })
    }

    ionViewDidLeave(){
        this.setAnalytics();
    }
    ionViewDidEnter(){
        // setting start time for analytics
        this.start_time_analytics = moment();
        this.event_provider.hidefooter(true, 'other');
    }
}
